.sce-ccc-meter{margin-top:15px;margin-bottom:15px;height:20px;position:relative;background:#EEE;-moz-border-radius:25px;-webkit-border-radius:25px;border-radius:25px;padding:10px;box-shadow:inset 0 -1px 1px rgba(255,255,255,0.3);overflow:hidden}
.sce-ccc-meter>span{top:0;left:0;display:block;height:100%;border-top-right-radius:8px;border-bottom-right-radius:8px;border-top-left-radius:20px;border-bottom-left-radius:20px;position:absolute}
.sce-ccc-meter.sce-ccc-valid span{background-color:#2bc253;background-image:linear-gradient(center bottom,#2bc253 37%,#54f054 69%);box-shadow:inset 0 2px 9px rgba(255,255,255,0.3),inset 0 -2px 6px rgba(0,0,0,0.4)}
.sce-ccc-meter.sce-ccc-invalid span{background-color:#e70000;background-image:linear-gradient(center bottom,#e70000 37%,#f00 69%);box-shadow:inset 0 2px 9px rgba(255,255,255,0.3),inset 0 -2px 6px rgba(0,0,0,0.4)}
/*# sourceMappingURL=sce-ccc-progress-bar.min.min.css.map */
